
.marked {
  font-size: large;
}
.marked p > img {
    width: 100%;
    margin-bottom: 10px;
    box-shadow: 5px 5px 3px #666;
}
.marked h1, .marked h2, .marked h3 {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.marked h1 {
    padding-bottom: 10px;
}
.marked h2 {
    padding-bottom: 5px;
}



